.cityList {
  position: absolute;
  margin-top:1px;
  width: 250px;
  background-color: #fff;
  opacity: 0.95;
  z-index: 2;
  color: #000;
}

.listLocation {
  padding: 10px 16px;
  border-bottom: solid 1px #D1D1D1;
}

.listLocation:hover {
  cursor: pointer;
  background-color: #f68422;
}